    p.mb-5 El accidente de trnsito es considerado un evento generalmente involuntario, generado al menos por un vehculo en movimiento que causa daos a personas y bienes involucrados en l e igualmente suele afectar la normal circulacin de otros vehculos que se movilizan por la va o vas comprendidas en el lugar o dentro de la zona de influencia del hecho. (Congreso Nacional, 2002).
