              p(data-aos="flip-up") De esta manera se puede indicar que la investigacin del accidente mantiene un estricto orden secuencial organizado en una metodologa, donde se definen las actuaciones de los investigadores apoyados en las ciencias y disciplinas aplicables para cada caso particular.
