          h3.mb-0.ms-3 Clasificacin de accidentes
    p.mb-5 Los accidentes en Colombia se clasifican en seis tipos que son:
    figure.mb-5
      .video
        iframe(width="560" height="315" src="https://www.youtube.com/embed/DWzEUuTfQAw" title="Clasificacin de los Accidentes de trnsito" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)
      figcaption Video. Clasificacin de los accidentes de trnsito
    .row.align-items-center.mb-4
      .col-auto.pe-0(style="z-index:2")
        figure
          img(src='@/assets/curso/tema1/img14.svg', style="width: 50px" ,alt='').m-auto
      .col-auto.bg-c7(style="z-index:1")
        .p-2
          h3.mb-0.ms-3 Variables importantes
    p.mb-5 La velocidad, la trayectoria, la altura del centro de gravedad, el tipo de vehculo y la condicin del peatn.
    .row.justify-content-center.mb-5
      .col-8.col-lg.bg-c5.m-2
        .p-4
          figure.mb-4
            img(src='@/assets/curso/tema1/img17.svg', style="width: 90px" ,alt='')
          p #[b Modalidades de impacto:]
          p.mb-0 Limpert (1999).
      .col-8.col-lg.bg-c5.m-2
        .p-4
          figure.mb-4
            img(src='@/assets/curso/tema1/img18.svg', style="width: 90px" ,alt='')
          p #[b Impacto primario:]
          p.mb-0 Entre el vehculo y el peatn, del que resultan las mayores lesiones.
      .col-8.col-lg.bg-c5.m-2
        .p-4
          figure.mb-4
            img(src='@/assets/curso/tema1/img19.svg', style="width: 90px" ,alt='')
          p #[b Impacto secundario:]
          p.mb-0 Entre el peatn y el suelo, que incluye su cinemtica y localizacin sobre la va.
    .row.align-items-center.mb-4
      .col-auto.pe-0(style="z-index:2")
        figure
          img(src='@/assets/curso/tema1/img14.svg', style="width: 50px" ,alt='').m-auto
      .col-auto.bg-c7(style="z-index:1")
        .p-2
          h3.mb-0.ms-3 #[b Impactos frontales]
    p.mb-5 Las fases en las que se ven envueltos los peatones son las siguientes:
    .row.justify-content-center.mb-5
      .col-md-8.col-lg.m-2
        .row
          .col-12.px-0
            figure
              img(src='@/assets/curso/tema1/img20.svg', style="width: 100%" ,alt='').m-auto
          .col-12.bg-c10
            .p-4
              p.text-center #[b Fases de impacto]
              p.text-center.text-desc El primer contacto con algunas de sus partes, el peatn es acelerado aproximadamente a la misma velocidad que el vehculo que le impacta.
      .col-md-8.col-lg.m-2
        .row
          .col-12.px-0
            figure
              img(src='@/assets/curso/tema1/img21.svg', style="width: 100%" ,alt='').m-auto
          .col-12.bg-c10
            .p-4
              p.text-center #[b Fase de vuelo]
              p.text-center.text-desc En la que, tras separarse el cuerpo del peatn del vehculo, inicia una fase de vuelo hasta que golpea con el suelo.
      .col-md-8.col-lg.m-2
        .row
          .col-12.px-0
            figure
              img(src='@/assets/curso/tema1/img22.svg', style="width: 100%" ,alt='').m-auto
          .col-12.bg-c10
            .p-4
              p.text-center #[b Fase de deslizamiento y/o rotacin]
              p.text-center.text-desc En la que, tras haber golpeado con el suelo, el cuerpo del peatn se desplazar, rodar o realizar una combinacin de ambos movimientos hasta que llegue a su posicin final, pudiendo haber golpeado con elementos que se haya encontrado a su paso.
    .row.mb-5
      .col-12.bg-c11
        .p-4
          .row.align-items-center
            .col.mb-4.mb-lg-0
              p.mb-0 Los impactos en el atropello tambin generan proyecciones que son objeto de estudio, puesto que a travs de estos se pueden medir velocidades, trayectorias, fuerzas de impacto, #[em momentum] y distancias; es as como, muchos autores han tratado de encontrar patrones de proyeccin de acuerdo a distintas variables preexistentes al momento del impacto.
            .col-lg-auto
              figure
                img(src='@/assets/curso/tema1/img23.svg', style="width: 90px" ,alt='').m-auto
    .row.align-items-center.mb-4
      .col-auto.pe-0(style="z-index:2")
        figure
          img(src='@/assets/curso/tema1/img14.svg', style="width: 50px" ,alt='').m-auto
      .col-auto.bg-c7(style="z-index:1")
        .p-2
          h3.mb-0.ms-3 Variables para atropellos reales
    .row.align-items-center.mb-4
      .col-auto.pe-0
        figure
          img(src='@/assets/curso/tema1/img24.svg', style="width: 10px" ,alt='').m-auto
      .col
        p.mb-0 #[b Trayectorias]
    p Para determinar las trayectorias pre-impacto de un vehculo, se puede realizar fcilmente con la ubicacin de huellas de frenada, derrape, aceleracin, arrastres de llanta o metlicos, as como tambin secuencialmente deber coincidir la trayectoria con la ubicacin de los vehculos implicados al momento del impacto.
    p.mb-5 Las cinemticas que describen un peatn en un atropello y definen la velocidad del impacto y sus consecuencias, as:
    .tarjeta.tarjeta--azul.mb-5
      .p-4.p-lg-5
        SlyderA(tipo="b")
          .row
            .col-12
              p #[b Trayectoria #[em Wrap] (envolver)]
              p.mb-4 Describe un envolvimiento del peatn sobre el vehculo, teniendo en cuenta que en el post-impacto, el cuerpo del peatn se arquea hacia la parte frontal del vehculo para posteriormente deslizarse sobre el cap, en este caso, los centros de gravedad no concuerdan, teniendo peatn con su centro por encima del rea de contacto inicial con el vehculo.
              figure
                img(src='@/assets/curso/tema1/img25.svg', style="width: 1150px" ,alt='').m-auto
          .row
            .col-12
              p #[b Trayectoria #[em forward projection] (proyeccin hacia adelante)]
              p.mb-4 Es una proyeccin hacia la parte de adelante, donde el peatn es atropellado por un vehculo tipo camin, bus, microbs o campero con su punto de contacto por encima del centro de gravedad.
              figure
                img(src='@/assets/curso/tema1/img26.svg', style="width: 1150px" ,alt='').m-auto
          .row
            .col-12
              p #[b Trayectoria #[em roof vault] (bveda de teho)]
              p.mb-4 #[em Roof vault] es una trayectoria particular que describe a un peatn al ser embestido por un vehculo que viaja a altas velocidades. Esta trayectoria se puede presentar bajo dos consecuencias, la primera es una aceleracin del automvil durante el contacto y la segunda es la no aplicacin de frenos al impacto.
              figure
                img(src='@/assets/curso/tema1/img27.svg', style="width: 1150px" ,alt='').m-auto
          .row
            .col-12
              p #[b Trayectoria #[em fender vault] (bveda de guardabarros)]
              p.mb-4 En este tipo de trayectoria, son los casos que se presentan principalmente en adultos o personas de talla grande, donde su impacto es la humanidad de la persona, en zonas cercanas al centro de gravedad, con el vrtice del guardapolvo, en el cual los vehculos camperos, camionetas y utilitarios son los que generalmente muestran esta dinmica.
              figure
                img(src='@/assets/curso/tema1/img28.svg', style="width: 1150px" ,alt='').m-auto
          .row
            .col-12
              p #[b Trayectoria #[em Somersault] (Voltereta)]
              p.mb-4 Conocida como el salto mortal esta trayectoria que describe el peatn al ser atropellado, especialmente por automviles, se presenta con gran violencia y sus consecuencias en su mayora son fatales, las principales lesiones se concentran en el crneo y la columna cervical.
              figure
                img(src='@/assets/curso/tema1/img29.svg', style="width: 1150px" ,alt='').m-auto
    .row.align-items-center.mb-4
      .col-auto.pe-0(style="z-index:2")
        figure
          img(src='@/assets/curso/tema1/img14.svg', style="width: 50px" ,alt='').m-auto
      .col-auto.bg-c7(style="z-index:1")
        .p-2
          h3.mb-0.ms-3 Clasificacin de los choques
    p.mb-5 Los choques se clasifican de dos maneras, de acuerdo a su tipologa de contacto y de acuerdo al cuerpo con el que colision, as:
    .row.align-items-center.mb-4
      .col-auto.pe-0
        figure
          img(src='@/assets/curso/tema1/img24.svg', style="width: 10px" ,alt='').m-auto
      .col
        p.mb-0 #[b Segn el tipo de contacto]
    p Para determinar las trayectorias pre-impacto de un vehculo, se puede realizar fcilmente con la ubicacin de huellas de frenada, derrape, aceleracin, arrastres de llanta o metlicos, as como tambin secuencialmente deber coincidir la trayectoria con la ubicacin de los vehculos implicados al momento del impacto.
    p.mb-5 Las cinemticas que describen un peatn en un atropello y definen la velocidad del impacto y sus consecuencias, as:
    .row.justify-content-center.mb-5
      .col-lg-10
        AcordionA(tipo="b" clase-tarjeta="tarjeta tarjeta--gris")
          .row(titulo="Choque frontal cntrico o centrado")
            .col-12
              p.mb-4 El choque frontal cntrico es aquel donde coincide la posicin longitudinal de los vehculos con centro en el frente de ambos.
              figure
                img(src='@/assets/curso/tema1/img30.svg', style="width: 1150px" ,alt='').m-auto
          .row(titulo="Choque frontal excntrico o descentrado")
            .col-12
              p.mb-4 Este tipo de choque es un encuentro longitudinal en su direccin, pero con la particularidad que el punto de contacto est dado en sus vrtices.
              figure
                img(src='@/assets/curso/tema1/img31.svg', style="width: 1150px" ,alt='').m-auto
          .row(titulo="Choque frontal angular")
            .col-12
              p.mb-4 Cuando los ejes longitudinales de los vehculos se encuentran de manera frontal formando un ngulo distinto a 90, se puede decir existe un choque frontal angular.
              figure
                img(src='@/assets/curso/tema1/img32.svg', style="width: 1150px" ,alt='').m-auto
          .row(titulo="Choque lateral perpendicular")
            .col-12
              p.mb-4 En este choque los ejes forman una ngulo de 90, sub dividindose a su vez en anteriores (parte delantera), centrales (parte central), posteriores (parte trasera).
              figure
                img(src='@/assets/curso/tema1/img33.svg', style="width: 1150px" ,alt='').m-auto
          .row(titulo="Choque lateral oblicuo")
            .col-12
              p.mb-4 Los ejes longitudinales en este choque, se encuentran en cualquiera de los laterales de uno de los vehculos, con el frente del otro, formando un ngulo diferente de 90.
              figure
                img(src='@/assets/curso/tema1/img34.svg', style="width: 1150px" ,alt='').m-auto
          .row(titulo="Choque tipo roce positivo")
            .col-12
              p.mb-4 El roce positivo es el encuentro que impacta principalmente la pintura y lmina de los vehculos, el roce es positivo porque los vehculos viajan en sentido de circulacin contrario.
              figure
                img(src='@/assets/curso/tema1/img35.svg', style="width: 1150px" ,alt='').m-auto
          .row(titulo="Choque tipo roce negativo")
            .col-12
              p.mb-4 El roce negativo es una colisin, donde friccionan los vehculos, ocasionando daos en la estructura superficial de los laterales, su caracterstica principal, es que los vehculos se encuentran viajando en el mismo sentido, como lo indica la imagen (Ramos Enriquez y Quiroga Amaya, 2017).
              figure
                img(src='@/assets/curso/tema1/img36.svg', style="width: 1150px" ,alt='').m-auto
          .row(titulo="Choque trasero o posterior cntrico")
            .col-12
              p.mb-4 El choque trasero o posterior, tambin es conocido como accidente por alcance y como su nombre lo indica, es el impacto entre dos vehculos, donde se impactan la parte frontal de uno de ellos con la parte trasera del otro, sin importar cul es el vehculo embistente y el vehculo embestido, teniendo en cuenta que se pueden presentar casos donde quien embiste es el vehculo afectado en su parte posterior por una reversa sin precaucin.
              figure
                img(src='@/assets/curso/tema1/img37.svg', style="width: 1150px" ,alt='').m-auto
          .row(titulo="Choque trasero o posterior excntrico")
            .col-12
              p.mb-4 Este tipo de accidente se presenta por alcance de los vehculos o mediante una reversa aplicada por el vehculo que se encuentra delante, como se explic anteriormente, con la diferencia que sus ejes longitudinales estn en paralelo pero no concuerdan en una superposicin; no obstante el golpe se presenta en los vrtices o entre los tercios anteriores izquierdo o derecho y tercios posteriores izquierdo o derecho de los implicados respectivamente.
              figure
                img(src='@/assets/curso/tema1/img38.svg', style="width: 1150px" ,alt='').m-auto
          .row(titulo="Choque trasero o posterior Oblicuo")
            .col-12
              p.mb-4 Para este caso, los vehculos coinciden en un impacto de los partes posterior y anterior, respectivamente, pero sus ejes longitudinales forman un ngulo que en todo caso debe ser diferente de 90.
              figure
                img(src='@/assets/curso/tema1/img39.svg', style="width: 1150px" ,alt='').m-auto
    .row.align-items-center.mb-4
      .col-auto.pe-0(style="z-index:2")
        figure
          img(src='@/assets/curso/tema1/img14.svg', style="width: 50px" ,alt='').m-auto
      .col-auto.bg-c7(style="z-index:1")
        .p-2
          h3.mb-0.ms-3 Clasificacin del choque segn el cuerpo con el que colisiona
    p.mb-5 De acuerdo al Manual para el diligenciamiento del informe policial de accidente de trnsito, aprobado por la Resolucin 0011268 de 2012 (Ministerio de Transporte, 2012), el choque debe ser especfico en su connotacin, es decir, se debe indicar con qu tipo de cuerpo u objeto colision el vehculo involucrado en el accidente; es as como, en el formato se muestra un campo 5.1 y 5.2 como se puede apreciar en la tabla:
    .row.justify-content-center.mb-5
      .col-lg-10
        .titulo-sexto.color-acento-contenido.mb-4
          p.mb-0 #[b Tabla 3]
          p.mb-0 Clasificacin de choque
        .tabla-a.color-secundario
          table
            caption.mt-3 Nota, Resolucin 11268. Ministerio de Transporte de Colombia,  (2012).
            thead
              tr
                th 5.1 Choque con
                th(colspan="3") 5.2 Objeto Fijo
            tbody(style="background-color: #FFFFFF")
              tr
                td.text-center Vehculo 1
                td.text-center Muro 5
                td.text-center Semforo 9
                td.text-center Tarima Caseta 13
              tr
                td.text-center Tren 2
                td.text-center Poste 6
                td.text-center Inmueble 10
                td.text-center Vehculo 14
              tr
                td.text-center Semoviente 3
                td.text-center rbol 7
                td.text-center Hidrante 11
                td.text-center Estacionado 15
              tr
                td.text-center Objeto Fijo 4
                td.text-center Baranda 8
                td.text-center Vala seal 12
                td.text-center Otro 16
    p.mb-5 Como se puede apreciar, en el campo 5.1 aparecen las opciones de choque con vehculo, tren, semoviente u objeto fijo, pero en caso de presentarse una colisin con un objeto fijo, es necesario sealar que tipo de objeto y por ende remitirse al campo 5.2, el cual especifica mediante once (11) opciones cual es el tipo de objeto fijo con el que ha colisionado el vehculo en cuestin, donde las opciones son: muro, poste, rbol, baranda, semforo, inmueble, hidrante, valla seal, tarima, caseta, vehculo estacionado y otro.
    .row.align-items-center.mb-4
      .col-auto.pe-0(style="z-index:2")
        figure
          img(src='@/assets/curso/tema1/img14.svg', style="width: 50px" ,alt='').m-auto
      .col-auto.bg-c7(style="z-index:1")
        .p-2
          h3.mb-0.ms-3 Clase de accidente otro
    p.mb-5 El tipo de accidente otro, es el vehculo que sale de la carretera y sin chocar previamente cae a un ro, precipicio o barranco, tambin, se puede clasificar en esta categora, los vehculos que son accidentados por un deslizamiento de tierra, cada de piedras de una montaa o aquellos arrastrados por una creciente de un ro o inundacin, atrapados en carretera por un terremoto o cualquiera que se presente en fenmenos naturales o artificiales.
    .row.align-items-center.mb-4
      .col-auto.pe-0(style="z-index:2")
        figure
          img(src='@/assets/curso/tema1/img14.svg', style="width: 50px" ,alt='').m-auto
      .col-auto.bg-c7(style="z-index:1")
        .p-2
          h3.mb-0.ms-3 Escenas del accidente de trnsito
    .row.mb-5
      .col-12.bg-c6
        .p-4
          .row.align-items-center
            .col-lg-9.mb-4.mb-lg-0
              ul.lista-ul.mb-0
                li.d-flex
                  i.fas.fa-chevron-right(style="color: #F0B62A")
                  |
                  p.mb-0 Estudio del accidente de trnsito, implica actividades de indagacin exhaustiva.
                li.d-flex
                  i.fas.fa-chevron-right(style="color: #F0B62A")
                  |
                  p.mb-0 Anlisis de los elementos materiales de prueba y/o evidencia fsica.
                li.d-flex
                  i.fas.fa-chevron-right(style="color: #F0B62A")
                  |
                  p.mb-0 Lugar de los hechos.
                li.d-flex
                  i.fas.fa-chevron-right(style="color: #F0B62A")
                  |
                  p.mb-0 Pruebas testimoniales.
                li.d-flex
                  i.fas.fa-chevron-right(style="color: #F0B62A")
                  |
                  p.mb-0 Pruebas documentales.
                li.d-flex
                  i.fas.fa-chevron-right(style="color: #F0B62A")
                  |
                  p.mb-0 Indicios que conlleven a establecer conjeturas coherentes y objetivas.
                li.d-flex
                  i.fas.fa-chevron-right(style="color: #F0B62A")
                  |
                  p.mb-0 Causas que originaron el accidente.
                li.d-flex
                  i.fas.fa-chevron-right(style="color: #F0B62A")
                  |
                  p.mb-0 Factores asociados a dichas causas.
                li.d-flex
                  i.fas.fa-chevron-right(style="color: #F0B62A")
                  |
                  p.mb-0 Tratamiento desde la criminalstica.
                li.d-flex
                  i.fas.fa-chevron-right(style="color: #F0B62A")
                  |
                  p.mb-0 Anlisis de enfoques multidisciplinarios con apoyo en ciencias como la fsica, la psicologa, la sociologa, las matemticas, entre otras como el aspecto jurdico.
                li.d-flex
                  i.fas.fa-chevron-right(style="color: #F0B62A")
                  |
                  p.mb-0 Desde el punto de vista de la fsica, el accidente de trnsito es un fenmeno asociado al movimiento de los cuerpos y su tratamiento las cuales develan el origen de las causas desde la perspectiva de la dinmica.
                li.d-flex
                  i.fas.fa-chevron-right(style="color: #F0B62A")
                  |
                  p.mb-0 El origen de un accidente de trnsito, es la capacidad de analizar su evolucin en el tiempo tratndose ste de un sistema fsico que tiene una conexin o interaccin de tipo causal, una evolucin continua desde que empieza el hecho hasta su consumacin y la asociacin con magnitudes fsicas medibles.
                li.d-flex
                  i.fas.fa-chevron-right(style="color: #F0B62A")
                  |
                  p.mb-0 Observacin secuencial de su evolucin en el espacio tiempo.
                li.d-flex
                  i.fas.fa-chevron-right(style="color: #F0B62A")
                  |
                  p.mb-0 Anlisis fraccionando el hecho en escenas temporo-espaciales.
            .col-lg-3
              figure
                img(src='@/assets/curso/tema1/img40.svg', style="width: 250px" ,alt='').m-auto
    .row.align-items-center.mb-4
      .col-auto.pe-0(style="z-index:2")
        figure
          img(src='@/assets/curso/tema1/img14.svg', style="width: 50px" ,alt='').m-auto
      .col-auto.bg-c7(style="z-index:1")
        .p-2
          h3.mb-0.ms-3 Escenas temporo-espaciales
    p.mb-5 Las siguientes escenas temporo-espaciales sirven para correlacionar los fenmenos intervinientes, asociar factores y dilucidar consecuencias en los accidentes de trnsito:
    .tarjeta.tarjeta--azul.mb-5
      .p-4.p-lg-5
        SlyderA(tipo="b")
          .row.justify-content-center
            .col-lg-5.mb-4.mb-lg-0
              p #[b Escena preliminar]
              p Es la etapa del accidente de trnsito donde el riesgo se manifiesta latentemente a los actores intervinientes, ya sean conductores o peatones, que pueden percibirlo a travs de los sentidos, esta escena es donde se recibe la informacin manifiesta de un riesgo.
            .col-lg-6
              figure
                img(src='@/assets/curso/tema1/img14.png', style="width: 650px" ,alt='').m-auto
          .row.justify-content-center
            .col-lg-5.mb-4.mb-lg-0
              p #[b Escena de reacciones mltiple]
              p La escena de reacciones mltiples para el investigador son el punto de partida para realizar anlisis de la posibilidad que existe de evitar el accidente y a que actor se puede atribuir la mayor posibilidad de implementar acciones contundentes que ofrezcan una reaccin eficiente.
            .col-lg-6
              figure
                img(src='@/assets/curso/tema1/img15.png', style="width: 650px" ,alt='').m-auto
          .row.justify-content-center
            .col-lg-5.mb-4.mb-lg-0
              p #[b Puesta en escena]
              p La puesta en escena se ubica donde haya indicios de las reacciones, por ejemplo, cuando se deja una huella de frenado, es necesario aplicar el modelo fsico para encontrar la distancia total de parada a partir del tiempo de reaccin y de esta manera se pueden ubicar los vehculos en un espacio tiempo para realizar los anlisis correspondientes.
            .col-lg-6
              figure
                img(src='@/assets/curso/tema1/img16.png', style="width: 650px" ,alt='').m-auto
          .row.justify-content-center
            .col-lg-5.mb-4.mb-lg-0
              p #[b Escena de materializacin del hecho]
              p Cuando no existe eficiencia en la determinacin de las acciones que se dieron en la escena de reacciones mltiples, el riesgo es incontrolable e intransferible y la amenaza se materializa en el accidente, lo cual produce un desequilibrio del movimiento de los cuerpos deliberando el caos.
            .col-lg-6
              figure
                img(src='@/assets/curso/tema1/img17.png', style="width: 650px" ,alt='').m-auto
          .row.justify-content-center
            .col-lg-5.mb-4.mb-lg-0
              p #[b Determinacin de daos]
              p Se contemplan los daos secuencialmente, determinando en este punto el contacto inicial, el dao primario, los daos secundarios, los golpes que causaron las lesiones o la muerte de las vctimas.
            .col-lg-6
              figure
                img(src='@/assets/curso/tema1/img18.png', style="width: 650px" ,alt='').m-auto
          .row.justify-content-center
            .col-lg-5.mb-4.mb-lg-0
              p #[b Escena de posiciones finales]
              p Es bsicamente el lugar de los hechos que encuentra el investigador de campo o la autoridad de polica judicial que conocen el caso, en esta escena se encuentran los elementos materiales de prueba o evidencia fsica para ser analizados y relacionarlos con las secuencias propias de las escenas anteriores.
            .col-lg-6
              figure
                img(src='@/assets/curso/tema1/img19.png', style="width: 650px" ,alt='').m-auto
          .row.justify-content-center
            .col-lg-5.mb-4.mb-lg-0
              p #[b Investigacin y secuencia lgica]
              p La ltima escena en la secuencia lgica del espacio tiempo donde transcurri el accidente de trnsito, es el punto de partida de la investigacin, teniendo en cuenta que el registro del informe policial de accidente de trnsito, el informe de investigador de campo y el lbum fotogrfico  muestran el lugar como fue hallado.
            .col-lg-6
              figure
                img(src='@/assets/curso/tema1/img20.png', style="width: 650px" ,alt='').m-auto
    .row.align-items-center.mb-4
