            .col-12
              p.mb-0 Presente Cdigo rige en todo el territorio nacional y regula la circulacin.
          .row(titulo="Cdigo Penal Ley 599 2000")
            .col-12
              p.mb-0 Si hay lesiones personales u homicidio en un accidente de trnsito, lo que lo convierte en un hecho delictivo, se debe poner a disposicin de la autoridad judicial de la jurisdiccin donde haya ocurrido el hecho.
          .row(titulo="Ley 906 de 2004")
            .col-12
              p.mb-0 Otorga las funciones de polica judicial de manera permanente y especial dentro de su jurisdiccin a las autoridades de trnsito, por ello las actividades que se desarrollan estn ligadas a los protocolos de polica judicial y deberes como criminalista de campo para procesar tcnicamente un lugar de los hechos.
          .row(titulo="Ley 906 de 2004 Art. 202")
            .col-12
              p.mb-0 Personas lesionadas o fallecidas, lo que lo convierte en un hecho delictivo que debe ser puesto a disposicin de la autoridad judicial de la jurisdiccin donde haya ocurrido el hecho.
          .row(titulo="Artculo 43 y 44 de la ley 769 de 2002")
            .col-12
              p.mb-0 Daos materiales y el procedimiento a realizar, el cual debe enfocarse directamente a tratar de resolver el incidente de manera rpida y conciliatoria, despejando la va lo ms rpido posible.
      .col-lg-4
        figure
          img(src='@/assets/curso/tema1/img21.png', style="width: 350px" ,alt='').m-auto
    .row.align-items-center.mb-4
