          h3.mb-0.ms-3 Accidente de solo daos materiales
    p.mb-4 El artculo 143 de la Ley 769 de 2002 Por la cual se expide el Cdigo Nacional de Trnsito Terrestre y se dictan otras disposiciones #[b indica  que  en todo accidente de trnsito donde slo se causen daos materiales en los que resulten afectados vehculos asegurados, no asegurados, inmuebles, cosas o animales y no se produzcan lesiones personales, los conductores, entidades aseguradoras y dems interesados en el accidente recaudar todas las pruebas relativas a la colisin mediante la utilizacin de herramientas tcnicas y tecnolgicas]. Para tal efecto, el material probatorio recaudado, reemplazar el informe de accidente de trnsito que expide la autoridad competente.
    .row.align-items-center
      .col-lg-4.mb-4.mb-lg-0
