              p.mb-0 Se debe retirar inmediatamente los vehculos colisionados y todo elemento que pueda interrumpir el trnsito y acudir a los centros de conciliacin debidamente autorizados por el Ministerio de Justicia y del Derecho.
        p.mb-0 Si fracasa la conciliacin, cualquiera de las partes puede acudir a los dems mecanismos de acceso a la justicia. Para tal efecto, no ser necesaria la expedicin del informe de accidente de trnsito, ni la presencia de autoridad de trnsito en la respectiva audiencia de conciliacin. (Congreso de la Repblica, 2002)
