    TabsB.color-acento-contenido.mb-5
      .py-4.py-md-5(titulo="Ley 769 Art. 3 de 2002" :icono="require('@/assets/curso/tema1/img41.svg')")
        .row
          .col-lg-6.mb-4.mb-lg-0
            p #[b Ley 769 Art. 3 de 2002]
            p.mb-0 En los casos donde se presenten hechos punibles en accidentes viales en Colombia, las autoridades operativas de trnsito en sus cuerpos de Agentes de trnsito y personal de la Direccin de Trnsito y Transporte de la Polica Nacional y donde no existiere autoridad operativa, las autoridades administrativas sern las encargadas de llevar a cabo el procedimiento de polica judicial, con arreglo a la Ley 906 Cdigo de procedimiento penal, cumpliendo los protocolos de criminalstica de campo para realizar los actos urgentes.
          .col-lg-6
            figure
              img(src='@/assets/curso/tema1/img23.png', style="width: 450px" ,alt='').m-auto
      .py-4.py-md-5(titulo="Ley 769 Art. 149 de 2002" :icono="require('@/assets/curso/tema1/img41.svg')")
        .row
          .col-12
            p #[b Ley 769 Art. 149 de 2002]
            p Se deber diligenciar el informe policial de accidentes de trnsito, siguiendo estrictamente las indicaciones estipuladas por el Manual para el diligenciamiento del informe policial de accidentes de trnsito avalado por la Resolucin 0011268 de 2012.
            p Es importante mencionar que esta norma tambin obliga a la autoridad que atiende el hecho punible en accidente de trnsito a enviar las partes implicadas (conductores) a la realizacin de la prueba de embriaguez y de no realizar dicho procedimiento sin causa justificada, esta omisin ser considerada como falta disciplinaria grave.
            p La atencin de estos casos debe dar prioridad a la proteccin de los derechos fundamentales de las vctimas e involucrados donde el derecho a la vida prima sobre los dems, la atencin pre hospitalaria, el traslado de heridos y salvaguardar a todos los que estn en el lugar de los hechos es prioridad como autoridad de trnsito.
            p Cuando se realice el Informe Policial de Accidente de Trnsito o el croquis, el funcionario deber entregar estos documentos inmediatamente a los interesados y a la autoridad competente en materia penal que es la fiscala, acotando que la Ley indica claramente que su omisin ser considerada como causal de mala conducta.
            p.mb-0 Luego del procesamiento de la escena cumpliendo los protocolos de criminalstica y del correcto diligenciamiento del IPAT, la autoridad de trnsito debe diligenciar los formatos de polica judicial, llevar a los conductores a la prctica del examen de embriaguez y poner las evidencias bajo cadena de custodia y llevarlas al almacn de evidencias de la fiscala general de la nacin.
    .row.mb-5
      .col-12.bg-c9
