            .col
              p.mb-0 Es por este motivo que en accidentes de trnsito, el arma con el cual se ocasionan lesiones u homicidios son los vehculos los cuales deben ir al parqueadero autorizado por la fiscala y ser la fiscala la que haga entrega del elemento una vez realizado el procedimiento tcnico.
