            p #[b Artculo 143 Ley 769 de 2002]
            p El procedimiento para la atencin de accidentes de trnsito de solo daos materiales donde se causen daos materiales en los que resulten afectados vehculos asegurados, no asegurados, inmuebles, cosas o semovientes, fue recientemente actualizado extractando los nuevos amparos jurdicos establecidos en el artculo 143 de la Ley 769 as:
            p #[i.txt-c1  los conductores, entidades aseguradoras y dems interesados en el accidente recaudar todas las pruebas relativas a la colisin mediante la utilizacin de herramientas tcnicas y tecnolgicas, que permitan la atencin del mismo en forma oportuna, segura y que garantice la autenticidad, integridad, conservacin y posterior consulta  y uso probatorio de la informacin. Para tal efecto, el material probatorio recaudado con estas condiciones reemplazar el informe de accidente de trnsito que expide la autoridad competente.]
        .row.mb-4
          .col-md-auto.mb-4.mb-lg-0
            figure
              img(src='@/assets/curso/tema1/img43.svg', style="width: 120px" ,alt='').m-auto
          .col
            p #[b Ley 2251 artculo 16 de 2022]
            p #[i.txt-c1  los conductores deben retirar inmediatamente los vehculos colisionados y todo elemento que pueda interrumpir el trnsito y acudir a los centros de conciliacin debidamente autorizados por el Ministerio de Justicia y del Derecho. Si fracasa la conciliacin, cualquiera de las partes puede acudir a los dems mecanismos de acceso a la justicia. Para tal efecto, no ser necesaria la expedicin del informe de accidente de trnsito, ni la presencia de autoridad de trnsito en la respectiva audiencia de conciliacin.]
            p Lo anterior busca descongestionar y agilizar los procedimientos, donde ahora los conductores no requieren la presencia de la autoridad de trnsito para el diligenciamiento del IPAT, solo tomarn ellos mismos los datos y remitirse a un centro de conciliacin para solucionar el impase.
        .row.mb-4
          .col-md-auto.mb-4.mb-lg-0
            figure
              img(src='@/assets/curso/tema1/img43.svg', style="width: 120px" ,alt='').m-auto
          .col
            p #[b Artculo 144 de la Ley 769 del 2002]
            p Sobre el Informe Policial se encuentra vigente, y establece que en los casos en que no fuere posible la conciliacin entre los conductores, el agente de trnsito que conozca el hecho levantar un informe descriptivo de sus pormenores, con copia inmediata a los conductores, quienes debern suscribirlas, y si stos se negaren a hacerlo bastar la firma de un testigo mayor de edad, por lo que ser el deber de la autoridad de trnsito que llegue a conocer el hecho asesorar a los conductores en la toma de la informacin para que vayan a un centro de conciliacin con las evidencias, pero tambin segn el caso, apoyar esta labor de toma de informacin diligenciando el IPAT.
