    p.mb-5 Por otra parte, el accidente de trnsito donde haya personas lesionadas o fallecidas debe ser atendido siguiendo los protocolos de polica judicial y puesto de manera inmediata a disposicin de la Fiscala General de la Nacin en la jurisdiccin donde haya ocurrido el hecho, siendo el deber de las autoridades de trnsito realizar el procedimiento, por esto es necesario recordar quines son las autoridades de trnsito en cada jurisdiccin as:
