    p.mb-5 Las normas de seguridad y salud en el trabajo aplicado a eventos de trnsito, la cual adopta la metodologa para el diseo, implementacin y verificacin de los planes estratgicos de seguridad vial, estableciendo que todas las entidades que cuenten para su funcionamiento con diez (10) o ms vehculos automotores, estn obligadas a desarrollar planes estratgicos de seguridad vial en funcin de su misionalidad y tamao, el cual debe estar articulado con el sistema de gestin de seguridad y salud en el trabajo SGSST quienes verificarn el cumplimiento del mismo en cada organizacin.
