        .col
          p.mb-0 Los procedimientos judiciales hacen referencia a todas las actuaciones de los servidores e investigadores en condicin de autoridad de trnsito (DITRA, agente de trnsito, inspector de polica, corregidor), que tienen funciones de polica judicial en accidentes de trnsito, donde haya personas lesionadas o fallecidas, por lo tanto, todos sus procedimientos se encuentran protocolizados a travs de la Fiscala General de la Nacin.
        .col-auto
