      .row(numero="2" titulo="Cdigo de Procedimiento Penal Artculo 205")
        .col
          p Los actos urgentes hacen referencia a todas las actuaciones por parte de las autoridades para atender el hecho punible, bien sea accidente con heridos o con personas fallecidas, estando estos actos reglamentados jurdicamente en el que reglamenta lo siguiente:
          p La finalidad de los actos urgentes es recolectar y embalar todos y cada uno de los elementos materiales de prueba y evidencias fsicas (EMP Y EF) encontradas en el lugar de los hechos, por lo que debe realizarse una documentacin descriptiva, fotogrfica y topogrfica del lugar y las evidencias para su posterior anlisis forense.
          p.mb-0 La norma tambin establece que cuando deba practicarse examen mdico-legal a la vctima o al indiciado, por ejemplo, descripcin de lesiones o examen de embriaguez, necropsia entre otros, es deber de la autoridad trasladar las personas al centro mdico oficial y autorizado por la fiscala para desarrollar el procedimiento, entendiendo que el cdigo de trnsito establece obligatoria la obligacin de realizar el examen de embriaguez a los conductores inmersos en un accidente de trnsito.
